import React, { useState } from "react";
import { WebDevelopment } from "../interfaces";
import { useTranslation } from "gatsby-plugin-react-i18next";

interface CollapsibleProps {
  development: WebDevelopment;
}

const Collapsible: React.FC<CollapsibleProps> = ({ development }) => {
  const { t } = useTranslation();

  return (
    <div className={styles.wrapper}>
      <ul className={styles.items}>
        {development.items.map((item, idx) => (
          <li key={idx} className="p-8">
            {item.title && (
              <h3 className={styles.trigger}>
                {t(`servicesPage.${item.title}`)}
              </h3>
            )}
            {item.copy && (
              <div className={`${styles.content} "text-center"}`}>{t(`servicesPage.${item.copy}`)}</div>
            )}
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Collapsible;

const styles = {
  wrapper: "flex relative z-20 max-w-[1024px] mx-auto",
  items: "grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4 mt-6 w-full",
  trigger: "flex flex-row p-0 rounded-lg text-md poppins-regular font-bold  text-center",
  content: "text-sm text-purplesemilight text-center mt-4 poppins-regular",
};
